import {
  CButton,
  CCard,
  CCardHeader,
  CCol,
  CRow,
  CTooltip,
  CInput,
  CForm,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CFormGroup,
  CLabel,
  CSelect,
  CCardBody,
} from "@coreui/react";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { CardBody } from "reactstrap";
import MaterialTable from "material-table";
import { useHistory, useLocation } from "react-router-dom";
import CIcon from "@coreui/icons-react";
import Select from "react-select";
import makeAnimated from "react-select/animated";

// import all action redux
import {
  GetIndikatorByIdAction,
  setFormPertanyaanAction,
  GetPertanyaanAction,
  SetDropdownJenisLokpriParseAction,
} from "src/common/redux/action";


const Pertanyaan = ({ match }) => {

   const animatedComponents = makeAnimated();

  const { formIndikatorReducer } = useSelector(
    (state) => state.indikatorReducer
  );
  const { dataComboJenisLokpriReducer } = useSelector(
    (state) => state.dropdownReducer
  );


  const {
    GetListPertanyaanReducer,
    modalPertanyaan,
    formPertanyaanReducer,
  } = useSelector((state) => state.pertanyaanReducer);

  const {
    IdPertanyaan,
    IdIndikator,
    Pertanyaan,
    BobotDefault,
    IdJenisLokri,
  } = formPertanyaanReducer;

  const dispatch = useDispatch();
      const [stateUpdate, setStateUpdate] = useState(false);

   const startLoad = () => {
    // dispatch(GetIndikatorByIdAction(match.params.id));
     dispatch(GetPertanyaanAction("GET_LIST_PERTANYAAN", match.params.id));
   };


  // set action
   const showModal = () =>
     dispatch({ type: "MODAL_PERTANYAAN", payload: !modalPertanyaan });


  const handleModalPertanyaan = (type, idPertanyaan) => {
    dispatch(SetDropdownJenisLokpriParseAction("DATA_CMB_LOKPRI_PARSE"));
    if (type === "add") {
      showModal();
      dispatch({ type: "RESET_FORM_PERTANYAAN" });

    } else {
    }
  };

  const prosesVariabelAdd = () =>{}
  const prosesVariabelEdit =() =>{}

  const prosesDelete = () => {}



  useEffect(() => {
    startLoad()
  },[dispatch])

  const columns = [
    {
      title: "Aksi",
      field: "internal_action",
      cellStyle: {
        backgroundColor: "#039be5",
        color: "#FFF",
      },
      headerStyle: {
        backgroundColor: "#039be5",
      },
      render: (rowData) => {
        return (
          <div className="row d-flex justify-content-center">
            <CTooltip content="Edit Indikator">
              <button
                type="button"
                className="btn btn-sm btn-dark text-center mr-2"
                onClick={() =>
                  handleModalPertanyaan("edit", rowData.idPertanyaan)
                }
              >
                <CIcon name="cil-pencil" /> Update
              </button>
            </CTooltip>
            <CTooltip content="Hapus Indikator">
              <button
                type="button"
                className="btn btn-sm btn-danger"
                onClick={() => prosesDelete(rowData.idPertanyaan, rowData.nama)}
              >
                <CIcon name="cil-drop" /> Hapus
              </button>
            </CTooltip>
          </div>
        );
      },
    },
    { title: "Pertanyaan", field: "pertanyaan" },
    { title: "Jenis Lokpri ", field: "jenisLokpri" },
    {
      title: "Bobot Default",
      field: "bobotDefault",
      render: (rowData) =>
        rowData.bobotDefault === null ? (
          <span className="badge badge-dark">0</span>
        ) : (
          <span className="badge badge-danger">{rowData.bobotDefault}</span>
        ),
    },
  ];

  return (
    <div>
      <CRow>
        <CCol md="12">
          <CCard>
            <CCardHeader>
              <CRow>
                <CCol md="9">
                  <h4>
                    <small>Indikator</small> {formIndikatorReducer.Nama}
                  </h4>
                </CCol>
                <CCol md="3">
                  <CRow>
                    <CCol md="5">
                      <CButton color="dark" variant="outline">
                        Kembali
                      </CButton>
                    </CCol>

                    <CCol md="6">
                      <CButton
                        color="primary"
                        onClick={() => handleModalPertanyaan("add")}
                      >
                        <FontAwesomeIcon icon="plus" />
                        Tambah
                      </CButton>
                    </CCol>
                  </CRow>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <MaterialTable
                title=""
                options={{
                  headerStyle: {
                    backgroundColor: "#01579b",
                    color: "#FFF",
                  },
                  filtering: true,
                }}
                columns={columns}
                data={GetListPertanyaanReducer}
              />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>

      <CModal show={modalPertanyaan} onClose={showModal} size="lg">
        <CModalHeader closeButton>
          <CModalTitle>
            {" "}
            {stateUpdate ? "Form Update" : "Form Tambah"}
          </CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CForm>
            <CFormGroup>
              <CLabel htmlFor="nf-nama">Nama</CLabel>
              <CInput
                type="text"
                id="nf-kode"
                name="nama"
                placeholder="input nama pertanyaan ..."
                value={Pertanyaan}
                onChange={(e) =>
                  dispatch(setFormPertanyaanAction("Nama", e.target.value))
                }
              />
            </CFormGroup>
            <CFormGroup>
              <CLabel htmlFor="jenis_lokpri"> Jenis Lokpri</CLabel>
              <Select
                closeMenuOnSelect={true}
                components={animatedComponents}
                onChange={(e) =>
                  dispatch(setFormPertanyaanAction("IdJenisLokri", e.value))
                }
                isMulti
                options={dataComboJenisLokpriReducer}
              />
            </CFormGroup>
          </CForm>
        </CModalBody>
        <CModalFooter>
          <CButton
            color="primary"
            onClick={stateUpdate ? prosesVariabelEdit : prosesVariabelAdd}
          >
            {stateUpdate ? "Update" : "Tambah"}
          </CButton>
          <CButton color="secondary" onClick={showModal}>
            Cancel
          </CButton>
        </CModalFooter>
      </CModal>
    </div>
  );
};

export default Pertanyaan;
